import React from 'react';

const CommentsList = ({comments}) => (
  <>
    <h3>Comments: </h3>
    {comments.map((comment, key) => (
      <div className="py-2 px-3 bg-light" key={key}>
        <h5>{comment.username}</h5>
        <p className="mb-0">{comment.text}</p>
      </div>
    ))}
  </>
);

export default CommentsList;
