import React from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => (
  <>
    <div className="row">
      <div className="col">
        <nav className="nav navbar-dark bg-dark">
          <ul className="nav">
            <li className="nav-item">
              <Link className="nav-link text-white" to="/">Home</Link>
            </li>
            <li>
              <Link className="nav-link text-white" to="/about">About</Link>
            </li>
            <li>
              <Link className="nav-link text-white" to="/articles-list">Articles</Link>
            </li>
            <li>
              <Link className="nav-link text-white" to="/sign-in">Sign In</Link>
            </li>
            <li>
              <Link className="nav-link text-white" to="/sign-up">Sign Up</Link>
            </li>
            <li>
              <Link className="nav-link text-white" to="/checkout">Checkout</Link>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </>  
)

export default NavBar;