import React, { useState } from 'react';

const AddCommentForm = ({articleName, setArticleInfo}) => {

  const [username, setUsername] = useState('');
  const [commentText, setCommentText] = useState('');
  
  const addComment = async () => {
    const result = await fetch(`api/articles/${articleName}/add-comment`, {
      method: 'post',
      body: JSON.stringify({username, text: commentText }),
      header: {
        'Content-Type': 'application/json',
      }
    });
    const body = await result.json();
    setArticleInfo(body);
    setUsername('');
    setCommentText('');
  }

  return (
    <div className="row">
      <div className="col-12">
        <h5>Add Comment</h5>
  
        <div className="form-group">
          <label htmlFor="name">Name: </label>
          <input className="form-control" id="name" type="text" value={username} onChange={(event) => setUsername(event.target.value)}></input>
        </div>
  
        <div className="form-group">
          <label htmlFor="comment">Comment: </label>
          <textarea className="form-control" id="comment" rows="3" spellCheck="false" value={commentText} onChange={(event) => setCommentText(event.target.value)}></textarea>
        </div>

        <button type="submit" className="btn btn-primary rounded-pill" onClick={() => addComment()}>Add Comment</button>
  
      </div>
    </div>
  );
}

export default AddCommentForm;